import Vue from 'vue'
import App from '@/App.vue'
import router from '@/router'

import VueScrollReveal from 'vue-scroll-reveal';
 

Vue.use(VueScrollReveal, {
  duration: 1500,
  distance: '200px',
  mobile: false
});


const vm = new Vue({
  el: '#app',
  router,

  render: h => h(App)
});

vm;
